* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Ropa Sans', sans-serif;
}

#root {
    background-color: rgba(16, 24, 32, 1);
    height: 100vh;
}

.container {
    max-width: 600px;
    background-color: rgba(39, 58, 77, 1);
    height: 100vh;
    margin: 0 auto;
}

.header {
    background-color: rgba(10, 16, 21, 1);
    height: 60px;
    color: white;
    position: fixed;
    width: 100%;
    max-width: 600px;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
    padding: 10px;
}

.error {
    font-weight: bolder;
}

.title {
    color: white;
    font-size: 2rem;
}

.signout {
    height: 45px;
    width: 100px;
    font-size: 1.2rem;
    background-color: rgba(16, 24, 32, 1);
    border: none;
    color: white;
    cursor: pointer;
}

.signin-cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
    background-color: rgba(39, 58, 77, 1);
    align-items: center;
}

.verifycontainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
    background-color: rgba(39, 58, 77, 1);
    align-items: center;
    padding-top: 90px;
}

#recaptcha-container {
    padding-bottom: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5px;
}

.phoneImg {
    width: 200px;
}

#sender {
    display: grid;
    align-items: center;
    justify-content: center;
}

#number {
    padding: 5px;
    font-size: 25px;
    background: none;
    border: 2px solid white;
    color: #fce100;
    font-family: inherit;
    letter-spacing: 2px;
    outline: none;
}

#sign-in-button {
    padding: 5px;
    margin-top: 10px;
    background-color: #fce100;
    border: none;
    font-family: inherit;
    font-size: 25px;
    cursor: pointer;
    transition: 0.2s ;
}

#sign-in-button:hover {
    font-weight: bold;
}

#verifier{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
}

#verificationcode{
    padding: 5px;
    font-size: 25px;
    background: none;
    border: 2px solid white;
    color: #fce100;
    font-family: inherit;
    letter-spacing: 2px;
    outline: none;
}

#verify{
    padding: 5px 10px;
    margin-top: 10px;
    background-color: #fce100;
    border: none;
    font-family: inherit;
    font-size: 25px;
    cursor: pointer;
    transition: 0.2s;
    letter-spacing: 3px;
    text-align: center;
}

.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 15px;
}

.welcome{
    letter-spacing: 1px;
    font-family: inherit;
    margin-bottom: 15px;
    font-size: 35px;
    color: white;
    padding: 10px;
}

.welcome-note{
    font-size: 15px;
    font-family: monospace;
    color: #fce100;
}

.paragraph{
    color: #ff4747;
    text-align: center;
    margin-top: 10px;
}

.isvisible{
    display: none;
}

.verify-note{
    margin-bottom: 16px;
    font-family: monospace;
    text-align: center;
    width: 228px;
    word-wrap: break-word;
    color: white;
}

#verify:hover{
    font-weight: bold;
}

.signin {
    background-color: rgba(16, 24, 32, 1);
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 7px 32px;
    font-size: 1.5rem;
    color: white;
}

.signin-cont p{
    font-size: 1.2rem;
    padding-top: 20px;
    color: rgba(10, 16, 21, 1);
    text-align: center;
}

.chatbox {
    padding-bottom: 60px;
    display: flex;
    flex-direction: column-reverse;
    gap: 15px;
    overflow: auto;
    max-height: 100vh;
    font-family: 'Poppins', sans-serif;
}

.img-cont{
    display: flex;
    align-items: center;
}

.img-cont img{
    width: 45px;
    border-radius: 50%;
}

.main-cont {
    display: flex;
    padding: 5px 5px;
    gap: 15px;
}

.name {
    font-size: 14px;
    letter-spacing: 1px;
    float: flex-end;
    color: darkgray;
}


.msg-cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(16, 24, 32, 1);
    color: white;
    padding: 2px 10px;
    border-radius: 10px;
}

.sent {
    flex-direction: row-reverse;
}

.footer {
    position: fixed;
    bottom: 0;
    z-index: 100;
    max-width: 600px;
    width: 100%;
    display: flex;
}

.footer input {
    max-width: 500px;
    width: 100%;
    height: 60px;
    background: rgba(10, 16, 21, 1);
    border: none;
    outline: none;
    font-size: 20px;
    color: white;
    padding: 0 10px;
}

.send{
    width: 100%;
    max-width: 100px;
    background-color: rgba(62, 92, 122, 1);
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.disable {
    cursor: not-allowed;
    background-color: rgb(55 67 80 / 98%);
}

.hiddenVerification {
    visibility: hidden;
}

.send img {
    width: 32px;
}

@media only screen and (max-width: 375px)
{
    .title{
        font-size: 1.5rem;
    }
    .signout{
        height: 40px;
        width: 80px;
        font-size: 1rem;
    }
    .img-cont img{
        width: 35px;
    }
    .msg-cont{
        gap: 0px;
    }
}

.chatbox::-webkit-scrollbar {
    width: 10px;
  }
.chatbox::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
.chatbox::-webkit-scrollbar-thumb {
    background-color: darkgrey;
}